.counter__container {
  width: 160px;
  border: 1px solid black;
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin: auto;
  border-radius: 20px;
  padding: 5px;
  gap: 5px;
}

.counter__btn {
  width: 150px;
  height: 50px;
  margin: 0 auto;
}

.counter__btn_next {
  margin-top: 30px;
} 




.counters__container {
  display: flex;
  flex-direction: column;
  gap: 5px;
}